import React from 'react';

const NavbarSlide = ({ slides, darkMode}) => {

    return (
        <div className="nav-wrapper position-relative end-0">
            <ul className="nav nav-pills nav-fill p-1" role="tablist" style={darkMode ? {background:'#232326d9', transition: 'background 1s ease'} : {background:'#fff', transition: 'background 1s ease'}}>
                {slides.map((slide, index) =>
                    <li key={index} className={`nav-item activeNav${slide.state}`}>
                        <a className='nav-link mb-0 px-0 py-1' data-bs-toggle="tab" href="#" role="tab"
                           aria-selected="true">
                            <span id={'span_'+index} className={`ms-1 ${slide.state}`} style={darkMode ? {color:'#fff'} : {}}>{slide.body}</span>
                        </a>
                    </li>
                )}
            </ul>
        </div>
    );
};

export default NavbarSlide;