import React, {useEffect, useState} from 'react';
import ListGroups from "../components/student/ListGroups";
import Nav from "../components/Nav";
import Card from "../components/Card";
import Body from "../components/Body";
import Loader from "../components/Loader";
import { CSSTransition } from 'react-transition-group';
import { ThemeContext, themes } from '../contexts/ThemeContext';
import NightsStayIcon from '@mui/icons-material/NightsStay';
import LightModeIcon from "@mui/icons-material/LightMode";

const Groups = () => {
    const [groups, setGroups] = useState({});
    const [isLoading, setLoading] = useState(true);


    const getGroups = async () => {
        await fetch("https://api.alfin-dk.ru/urtk/groups/get").then(res => res.json()).then(
            res => {
                setGroups(res);
                setLoading(false);
            }
        )
    }


    useEffect(() => {
        getGroups();
    }, [])
    return (
            <>
                <CSSTransition
                        in={!isLoading}
                        timeout={500}
                        classNames="body"
                        unmountOnExit
                    >
                    <ThemeContext.Consumer>
                        {({ changeTheme, theme, darkMode }) => (
                        <Body>
                            <Nav>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                    <a onClick={() => {
                                        changeTheme(darkMode ? themes.light : themes.dark);
                                    }} className={`btn ${theme}`}
                                       style={{padding: '0.8rem', borderRadius: '5rem'}}>
                                        {darkMode
                                            ? <LightModeIcon style={{color: '#dee1e4', marginRight: '0px'}} width={16}
                                                            height={16}/>

                                            : <NightsStayIcon style={{color: '#003892', marginRight: '0px', paddingLeft: '3px'}} width={16}
                                                             height={16}/>
                                        }
                                    </a>
                        </Nav>
                        <Card title="Расписание УРТК" subtitle="Добро пожаловать!" >
                            <ListGroups title="Выберите группу" darkMode={darkMode} theme={theme} json={groups}/>
                        </Card>
                    </Body>
                        )}
                </ThemeContext.Consumer>
                </CSSTransition>

                <CSSTransition
                    in={isLoading}
                    timeout={500}
                    classNames="loader"
                    unmountOnExit
                >
                    <Loader/>
                </CSSTransition>
            </>

    );
};
/*{isLoading
    ? <Loader/>
    : <Body>
        <Nav/>
        <Card title="Расписание УРТК" subtitle="Добро пожаловать!" >
            <ListGroups data={groups}/>
        </Card>
    </Body>

}*/
export default Groups;