// eslint-disable-next-line no-unused-vars
import React, {useEffect, useState} from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import '../../css/swiper.css';
import DaySchedulItemTeacher from "./DaySchedulItemTeacher";
// eslint-disable-next-line no-unused-vars

const ScheduleListTeacher = ({schedules, theme, darkMode, setDayWeekID}) => {
    let deyItemId = 0;
    let toDay = new Date().getDay();


    return (
        <Swiper
            onSwiper={(e) => {toDay == 0 ? e.activeIndex = 0 : e.activeIndex = (toDay - 1); setDayWeekID(e.activeIndex)}}
            onSlideChange={(e) => setDayWeekID(e.activeIndex)}
            className="slider"
        >
            {schedules.map((day, index) =>

                <SwiperSlide key={index} style={{padding: '2rem'}}>

                    <div className="row mt-lg-n10 mt-md-n12 mt-n12">
                        <div className="col-xl-4 col-lg-5 col-md-7 mx-auto">
                            <div className={`card z-index-0 ${theme}`}>
                                <input id='1' hidden defaultValue={day.title} />
                                <div className="card-body">
                                    {/* eslint-disable-next-line array-callback-return */}
                                    {day.pairs.map((sched, index) =>{
                                        if(sched == null ){
                                        }else{
                                            deyItemId = deyItemId + 1;
                                            return(
                                                <>
                                                    <DaySchedulItemTeacher end_time={sched.end_time} start_time={sched.start_time} darkMode={darkMode} idItem={deyItemId} time={sched.time} lesson={sched.lesson} group={sched.group} classroom={sched.classroom}/>
                                                </>
                                            );
                                        }if((index+1) == day.pairs.length && sched == null){

                                            return(
                                                <div className="d-flex" style={{justifyContent: 'center'}}>
                                                    <h6 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="mb-0">Пары отсутствуют<br/>
                                                    </h6>
                                                </div>
                                            );
                                        }
                                       }
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </SwiperSlide>
            )}
        </Swiper>
    );
};

export default ScheduleListTeacher;