import React from 'react';
import Body from "../components/Body";
import Nav from "../components/Nav";
import {ThemeContext, themes} from "../contexts/ThemeContext";
import LightModeIcon from "@mui/icons-material/LightMode";
import NightsStayIcon from "@mui/icons-material/NightsStay";
import Card from "../components/Card";
import {Link} from "react-router-dom";

const Home = () => {
    return (
        <ThemeContext.Consumer>
            {({ changeTheme, theme, darkMode }) => (
                <Body>
                    <Nav>
                        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                        <a onClick={() => {
                            changeTheme(darkMode ? themes.light : themes.dark);
                        }} className={`btn ${theme}`}
                           style={{padding: '0.8rem', borderRadius: '5rem'}}>
                            {darkMode
                                ? <LightModeIcon style={{color: '#dee1e4', marginRight: '0px'}} width={16}
                                                 height={16}/>

                                : <NightsStayIcon style={{color: '#003892', marginRight: '0px', paddingLeft: '3px'}} width={16}
                                                  height={16}/>
                            }
                        </a>
                    </Nav>
                    <Card title="Расписание УРТК" subtitle="Добро пожаловать!" >
                        <div className="container">
                            <div className="row mt-lg-n10 mt-md-n11 mt-n10">
                                <div className="col-xl-4 col-lg-5 col-md-7 mx-auto">
                                    <div className={`card z-index-0 ${theme}`}>
                                        <div className={`card-header text-center pt-4`}>
                                            <h5 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="card-text">Кто вы?</h5>
                                        </div>
                                        <div className="container-fluid" style={{marginBottom: '2rem'}}>
                                            <div className="nav-wrapper position-relative end-0">
                                                <Link to="/groups"
                                                      className="btn bg-gradient-info w-100 mt-4 mb-0">
                                                    Студент
                                                </Link>
                                                <Link to="/teacher" className="btn bg-gradient-info w-100 mt-4 mb-0">
                                                    Преподаватель
                                                </Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Card>
                </Body>
            )}
        </ThemeContext.Consumer>
    );
};

export default Home;