import React, { useState, useEffect } from 'react';
import { ThemeContext, themes } from './ThemeContext';


export default function ThemeContextWrapper(props) {
    //const [theme, setTheme] = useState(themes.dark);

    const [theme, setTheme] = useState();
    const [darkMode, setDarkMode] = useState();
    const [idDayWeek, setIdDayWeek] = useState(0);
    const [dayWeek, setDayWeek] = useState('');

    function setDayWeekID(id) {
        setIdDayWeek(id);
    }

    function changeTheme(theme) {
        setTheme(theme);
        localStorage.setItem('theme', theme);
        setDarkMode(!darkMode);
        localStorage.setItem('darkMode', !darkMode);
    }

    useEffect(() => {
        if(localStorage.getItem('theme') != null){
            setTheme(localStorage.getItem('theme'));

        }else{
            localStorage.setItem('theme', themes.light);
            setTheme(localStorage.getItem('theme'));
        }
        if(localStorage.getItem('darkMode') != null){
            setDarkMode(JSON.parse(localStorage.getItem('darkMode')));
        }else{
            localStorage.setItem('darkMode', false);
            setDarkMode(JSON.parse(localStorage.getItem('darkMode')));
        }
    }, [])

    useEffect(() => {
        switch (idDayWeek) {
            case 0:
                setDayWeek('Понедельник');
                break;
            case 1:
                setDayWeek('Вторник');
                break;
            case 2:
                setDayWeek('Среда');
                break;
            case 3:
                setDayWeek('Четверг');
                break;
            case 4:
                setDayWeek('Пятница');
                break;
            case 5:
                setDayWeek('Суббота');
                break;
        }
    }, [idDayWeek]);

    useEffect(() => {
        switch (theme) {
            case themes.light:
                document.body.classList.remove('dark-body');
                document.body.classList.add('ligth-body');
                break;
            case themes.dark:
                document.body.classList.remove('ligth-body');
                document.body.classList.add('dark-body');
                break;
        }
    }, [theme]);


    return (
        <ThemeContext.Provider value={{ theme: theme, darkMode: darkMode, dayWeek: dayWeek, changeTheme: changeTheme, setDayWeekID: setDayWeekID}}>
            {props.children}
        </ThemeContext.Provider>
    );
}