import React, {useEffect, useState} from 'react';
// eslint-disable-next-line no-unused-vars
import {Link, useNavigate, useParams} from "react-router-dom";
import Nav from "../components/Nav";
import Card from "../components/Card";
import Body from "../components/Body";
import ScheduleList from "../components/student/ScheduleList";
import Loader from "../components/Loader";
import {CSSTransition} from "react-transition-group";
import { ThemeContext, themes } from '../contexts/ThemeContext';
import NightsStayIcon from '@mui/icons-material/NightsStay';
import LightModeIcon from '@mui/icons-material/LightMode';
import NavbarSlide from "../components/NavbarSlide";

const Schedules = () => {
    let navigate = useNavigate();
    const group = useParams();
    const [isLoading, setLoading] = useState(true);
    const [schedule, setSchedule] = useState({});

    // eslint-disable-next-line react-hooks/exhaustive-deps
    const schedulesFetch = async () => {
        await fetch("https://api.alfin-dk.ru/urtk/student/shedule/get?group="+group.group).then(res => res.json()).then(
            res => {
                if(res.error === "Bad group name see ip:port/urtk/groups/get"){
                    navigate("/");
                }else{
                    setSchedule(res);
                    setLoading(false);
                }
            }
        )
    }

    useEffect(() => {
        schedulesFetch();
    }, [])

    return (
        <div>
            <CSSTransition
                in={!isLoading}
                timeout={500}
                classNames="body"
                unmountOnExit
            >
                <ThemeContext.Consumer>
                    {({ changeTheme, theme, darkMode, dayWeek,setDayWeekID }) => (
                        <Body>
                            <Nav>
                                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                                <a onClick={() => {
                                    changeTheme(darkMode ? themes.light : themes.dark);
                                }} className={`btn ${theme}`}
                                   style={{padding: '0.8rem', borderRadius: '5rem'}}>
                                    {darkMode
                                        ? <LightModeIcon style={{color: '#dee1e4', marginRight: '0px'}} width={16}
                                                         height={16}/>

                                        : <NightsStayIcon style={{color: '#003892', marginRight: '0px'}} width={16}
                                                        height={16}/>
                                    }
                                </a>
                            </Nav>
                            <Card navSlider={<NavbarSlide darkMode={darkMode} slides={[{body: 'Расписание', state: 'Slide'},{body: 'Консультации', state: ''}]}/>} title={dayWeek} subtitle={<Link style={{color: '#fff'}} to='/'>{`Группа: ${schedule.title}`}</Link>} >
                                <ScheduleList setDayWeekID={setDayWeekID} darkMode={darkMode} theme={theme} schedules={schedule.schedule}/>
                            </Card>
                        </Body>
                    )}
                </ThemeContext.Consumer>
            </CSSTransition>

            <CSSTransition
                in={isLoading}
                timeout={500}
                classNames="loader"
                unmountOnExit
            >
                <Loader/>
            </CSSTransition>
        </div>


    );
};

export default Schedules;