import React, {useEffect, useState} from 'react';

const DaySchedulItemTeacher = ({idItem, time, lesson, group, classroom, darkMode, start_time, end_time}) => {
    time = time.split('-');
    const startTime = useState(time[0]);
    const endTime = useState(time[1]);
    const [lesonState, setLesonState] = useState();
    const date = Math.floor(Date.now() / 1000);
    useEffect(()=>{
        if(date >= start_time && date <= end_time){
            setLesonState(true);
        }else {
            setLesonState(false);
        }
    }, [date])

    return (
        <>
            <div key={idItem} id={idItem} className="d-flex">
                <h6 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="mb-0 me-3">{startTime}<br/>{endTime}</h6>
                <h6 style={darkMode ? {color: '#dee1e4'} : {color: '#354766'}} className="mb-0">{lesson}<br/>
                    <small style={darkMode ? {color: '#6c9596'} : {color: '#8392AB'}} className="font-weight-normal">{group}</small>
                </h6>
                {classroom
                    ? <div className={lesonState ? `bg-gradient-success ms-auto` : `bg-gradient-info ms-auto`}
                           style={{borderRadius: '10px', padding: '0.9rem', height: 'max-content'}}>
                        {/* eslint-disable-next-line no-script-url,jsx-a11y/anchor-is-valid */}
                        <a className="ms-auto" title="Это аудитория" style={{color: 'white'}}>
                            {classroom}
                        </a>
                    </div>
                    : <div></div>
                }
            </div>
            <hr className="horizontal dark"/>
        </>
    );
};

export default DaySchedulItemTeacher;